export default {
	data() {
		return {
			more: false,
			pageNum: 1,
			pageSize: 20,
			loading: true,
			list: []
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getCoupon(item, i) {
			this.$Fetch({
				url: 'Coupon/getCoupon',
				data: { id: item.id },
				success: res => {
					item.num = item.num - 1;
					if (item.num === 0) {
						this.list.splice(i, 1);
					}
					uni.showModal({
						content: '兑换成功',
						confirmText: '继续兑换',
						cancelText: '返回',
						success: function(ret) {
							if (ret.cancel) {
								uni.navigateBack({});
							}
						}
					});
				}
			});
		},
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'Coupon/getData',
					data: { pageNum: this.pageNum, pageSize: this.pageSize },
					success: res => {
						if (res.length > 0) {
							this.list = this.list.concat(res);
							if (res.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						} else {
							this.more = false;
						}
					}
				});
			}
		}
	}
};
